import React, { useState, useEffect } from "react";

import "./photoBlock.scss";

const PhotoBlock = (props) => {
  const [imageState, setImageState] = useState({ image: [] });

  //grab photos
  useEffect(() => {
    props.photos.forEach((i) => {
      import(`../../../images/${i}.jpg`).then((image) => {
        setImageState((prevState) => ({
          image: [image.default, ...prevState.image],
        }));
      });
    });
  }, [props.photos]);

  //setup html with photos
  const photoNames = Object.values(imageState.image);

  let photoBlockDesktop = photoNames.map((src) => (
    <img src={src} alt="" className="photo-block__photo" key={src} />
  ));

  let photoBlockMobile = photoNames.map((src) => (
    <div className="photo-block__slides" key={src}>
      <img src={src} alt="" className="photo-block__photo--mobile" />
    </div>
  ));

  let photoBlockThumbnail = photoNames.map((src, index) => (
    <div className="photo-block__thumbnail__column" key={src}>
      <img
        className="thumbnail photo-block__photo--thumbnail"
        src={src}
        alt=""
        onClick={() => currentSlide(index)}
      />
    </div>
  ));

  //funcs for carosal 
  const checkForHtml = () => {
    if (document.getElementsByClassName("photo-block__slides").length> 0 && document.getElementsByClassName("thumbnail").length > 0) {
      showSlides(slideIndex)
      clearInterval(htmlInterval)
    }
  }

  let htmlInterval = setInterval(checkForHtml, 1000);

  let slideIndex = 0;

  const plusSlides = n => {
    showSlides((slideIndex += n));
  }

  const currentSlide = n => {
    showSlides((slideIndex = n));
  }

  const showSlides = n => {
    let slides = document.getElementsByClassName("photo-block__slides");
    var dots = document.getElementsByClassName("thumbnail");
    if (n > slides.length-1) {
      slideIndex = 0;
    }
    if (n < 0) {
      slideIndex = slides.length-1;
    }
    for (let i = 0; i < slides.length; i++) {
      slides[i].style.display = "none";
    }
    for (let i = 0; i < dots.length; i++) {
      dots[i].className = dots[i].className.replace(" active", "");
    }
    slides[slideIndex].style.display = "block";
    dots[slideIndex].className += " active";
  }

  return (
    <div className="photo-block">
      <div className="photo-block--desktop">{photoBlockDesktop}</div>
      <div className="photo-block--mobile">
        {photoBlockMobile}

        {/* <!-- Next and previous buttons --> */}
        <button className="photo-block__prev-btn" onClick={() => plusSlides(-1)}>
          &#10094;
        </button>
        <button className="photo-block__next-btn" onClick={() => plusSlides(1)}>
          &#10095;
        </button>

        {/* <!-- Thumbnail images --> */}
        <div className="photo-block__thumbnail__row">{photoBlockThumbnail}</div>
      </div>
    </div>
  );
};

export default PhotoBlock;
